@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.title {
  color: #000000;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: bold;
}
.image {
  display: block;
}
.title-desktop {
  color: #000000;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
}
.label {
  line-height: 26px;
  text-transform: uppercase;
}
.input {
  background-color: #f2f2f2;
  min-height: 58px;
  box-sizing: border-box;
  padding-left: 18px;
  border: 4px solid #f2f2f2;
}
.input:hover {
  background-color: #e5e5e5;
  border: 4px solid #e5e5e5;
}
.input:active,
.input:focus-within {
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
}
.popup-text {
  text-transform: uppercase;
  text-align: center;
  line-height: 24px;
}
.button {
  border: none;
  background-color: #81b3d2;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-align: center;
  min-height: 58px;
}
.button:hover,
.button:focus {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled,
.button--disabled {
  background-color: #e5e5e5;
  cursor: not-allowed;
  pointer-events: none;
}
:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}
.container {
  width: 282px;
