.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  min-height: 223px;
  position: relative;
}
.main-nav__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  width: 100%;
  height: 7px;
}
.main-nav__menu-bar {
  position: relative;
  background-color: #ffffff;
  z-index: 1;
  height: 56px;
  width: 100%;
}
.main-nav--opened .main-nav__menu-bar {
  position: static;
}
.main-nav__logo {
  display: block;
  width: 102px;
  margin: 0 auto;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__logo {
    z-index: 4;
  }
}
.main-nav--opened .main-nav__toggle {
  position: absolute;
  border: none;
  background-color: transparent;
  height: 57px;
  width: 57px;
  top: 0;
  right: 0;
  z-index: 3;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 25px;
  right: 16px;
  width: 29px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav__wrapper {
  position: relative;
}
.main-nav--closed .main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  width: 64px;
  height: 56px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  right: 20px;
  top: 16px;
  content: "";
  width: 22.9px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
.main-nav__item {
  background-color: #81b3d2;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  text-align: center;
  padding: 7px 0;
}
.main-nav__item:hover,
.main-nav__item:focus-within {
  background-color: #669ec0;
}
.main-nav__item:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.main-nav__link {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 40px;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
}
@media (max-width: 767px) {
